import Vue from "vue";
import Router, { RouterOptions } from "vue-router";

Vue.use(Router);

const routeConfig: RouterOptions = {
  mode: "history",
  base: qTouch.basePath + qTouch.appPathMap.locationManage,
  routes: [
    {
      path: "/",
      name: "openPath",
      component: () => import("./views/LocationManagement.vue")
    }
  ]
};

export default routeConfig;
